* {
  margin: 0;
  padding: 0;
}
.contacts {
  background: linear-gradient(180deg, #ffffff 85px, #fff5f6 85px);
  width: 100%;
  justify-content: center;

  &__card {
    width: 220px;
    height: 427px;
    background: #ffffff;
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 30px;
    padding-top: 43px;
    padding-left: 20px;
  }
  &__label {
    width: 80px;
    height: 16px;
    background: #f0d1a5;
    border-radius: 4px;
    font-size: 12px;
    line-height: 1.83;
    display: flex;
    justify-content: center;
    align-items: center;
    letter-spacing: 0.04em;
    color: #ffffff;
    margin-bottom: 25px;

    &--color {
      width: 130px;
      background: #c2e297;
    }
  }
  &__heading {
    font-size: 12px;
    line-height: 1.83;
    letter-spacing: 0.04em;
    color: #000000;
    margin-bottom: 10px;
  }
  &__text {
    font-weight: 500;
    width: 179px;
    font-size: 12px;
    line-height: 1.5;
    color: #907e82;
  }
  &__border {
    border-top: 1px solid #e1e1e1;
    border-bottom: 1px solid #e1e1e1;
    margin: 25px 20px 25px 0;
    padding-top: 25px;
    padding-bottom: 25px;
    list-style: none;
  }
  &__open-days {
    font-size: 12px;
    line-height: 1.83;
    color: #000000;
  }
  &__item li {
    &:first-child {
      margin-bottom: 10px;
    }
  }
  &__open-hour {
    font-size: 12px;
    line-height: 1.83;
    color: #ffa5ba;
  }
  &__links {
    list-style: none;
    & a {
      text-decoration: none;
      font-size: 12px;
      line-height: 1.5;
      color: #907e82;
    }
  }
  &__btn-container {
    display: flex;
    flex-direction: column;
    gap: 10px;
    justify-content: center;
    margin-top: 54px;
  }
  &__btn-locations {
    width: 161.45px;
    height: 44px;
    background: #d41342;
    border-radius: 22px;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.31;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #ffffff;
    border: transparent;
    margin: 0 auto;
  }
  &__btn-franchise {
    width: 161px;
    height: 44px;
    border-radius: 22px;
    background: #ffffff;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.31;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #d41443;
    border: transparent;
    margin: 0 auto;
    &:hover,
    &:focus {
      background: #ecebeb;
    }
  }
}

@media screen and (min-width: 768px) {
  .contacts {
    background: linear-gradient(180deg, #ffffff 78px, #fff5f6 78px);

    &__cards-container {
      display: flex;
      flex-wrap: wrap;
      gap: 20px;
      margin-left: 34px;
      margin-right: 34px;
    }

    &__card {
      margin: 0;
      flex-basis: calc((100% - 40px) / 3);
      height: 544px;
      padding-left: 31px;
      padding-top: 50px;

      margin-bottom: 81px;
    }
    &__label {
      width: 73px;
      height: 28px;
      border-radius: 6px;
      margin-bottom: 30px;
      font-size: 16px;
      line-height: 1.88;
      &--color {
        width: 138px;
        background: #c2e297;
      }
    }
    &__heading {
      font-size: 14px;
      line-height: 1.88;
      margin-bottom: 14px;
    }
    &__text {
      font-size: 14px;
      line-height: 1.64;
      padding-right: 38px;
    }
    &__border {
      margin: 30px 32px 30px 0;
      padding-top: 30px;
      padding-bottom: 30px;
    }
  }
}

